<template>
<div v-if="hasError" style="margin: auto; text-align: center; font-weight: bold; font-size: 26px; padding-top:30%; color:red;">
  {{errorMessage}}
</div>
<div v-else>
  <div v-if="paginationRequired()" style="text-align: center;">
    <span v-if="showPreviousPage()" v-on:click="updatePage(currentPage-1)" style="cursor: pointer;"> 
      < 
    </span>
      {{currentPage + 1}} of {{totalPages()}} 
    <span v-if="showNextPage()" v-on:click="updatePage(currentPage+1)" style="cursor: pointer;">
      >
    </span>
  </div>
  <v-expansion-panels>
    <v-expansion-panel
      v-for="ticket in visibleTickets"
      :key="ticket.id"
    >
      <v-expansion-panel-header>
        {{ticket.id}} - {{ticket.subject}}
      </v-expansion-panel-header>
      <v-expansion-panel-content> 
        <b>STATUS</b> - {{ticket.status}} <br>
        <b>DESCRIPTION</b> - {{ticket.description}} <br>
        <b>CREATED ON</b> - {{getFormattedDate(ticket.created_at)}} <br>
        <b>PRIORITY</b> - {{ticket.priority}} <br>
        <b>TAGS</b> - || <span v-for="(tag,i) in ticket.tags" :key="i"> {{tag}} || </span>
         
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
</div>
</template>

<script>
import API from '../api';
import moment from 'moment';
  export default {
    name: 'Home',
    data() {
      return{
        tickets:[],
        currentPage: 0,
        pageSize: 25,
        visibleTickets: [],
        hasError: true,
        errorMessage: ""
      };
    },
    async created(){
      try
      {
        let response = await API.getAllTickets();
        //check 500 
        console.log(response);
        if(response.status == 200)
        {
          this.hasError=false;
          this.tickets = response.data.tickets;
          if(response.data.count > 100)
          {
            let totalPages = Math.ceil(response.data.count/100);
            for(let i=2; i<=totalPages; i++)
            {
              let newTickets = await API.getTicketsByPage(i);
              //check 500
              if(newTickets.status == 200)
              {
                this.tickets = this.tickets.concat(newTickets.data.tickets);
              }
              else
              {
                this.hasError = true;
                break;
              }
            }
          }
          else
          {
            this.hasError = true;
          }
          if(!this.hasError)
          {
            this.updateVisibleTickets();
          }
          else
          {
            this.errorMessage = "Currently the server is facing issues. Please try again after some time or contact us at https://www.zendesk.com/contact/ if the issue still persists";
          }
        }
      }
      catch (error){
        console.log(error);
        this.errorMessage = "Currently the server is facing issues. Please try again after some time or contact us at https://www.zendesk.com/contact/ if the issue still persists";
        this.hasError = true;
      }
    },
    methods: {
      getFormattedDate(value){
        if(value){
          return moment(String(value)).format('LLL');
        }
      },
      updatePage(newPage){
        this.currentPage=newPage;
        this.updateVisibleTickets();
      },
      updateVisibleTickets(){
        this.visibleTickets = this.tickets.slice(this.currentPage*this.pageSize, (this.currentPage*this.pageSize)+this.pageSize);

        if(this.visibleTickets.length == 0 && this.currentPage > 0 && this.tickets.length > 0){
          this.updatePage(this.currentPage-1);
        }
      },
      paginationRequired(){
        return this.tickets.length > 25;
      },
      totalPages(){
        return(Math.ceil(this.tickets.length/this.pageSize));
      },
      showPreviousPage(){
          return this.currentPage == 0 ? false : true;
      },
      showNextPage(){
          return this.currentPage == (this.totalPages() - 1) ? false : true;
      }
    }
  }
</script>
